<template>
  <div>
    <div>TEST</div>
    <input class="flex border-2 border-black" type="file" @change="onFile" />
    <button class="flex border-2 border-black" @click="onUpload()">
      upload
    </button>
    <div class="flex border-2 border-black">{{ resultMessage }}</div>

    <line-chart :data="chartData"></line-chart>
    <pie-chart :data="pieData"></pie-chart>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedFile: null,
      resultMessage: "result",
      chartData: [
        ["한", 4],
        ["글", 2],
        ["Mar", 10],
        ["Apr", 5],
        ["May", 3],
      ],
      pieData: [
        ["no", 150],
        ["yes", 120],
      ],
    };
  },
  methods: {
    onFile(event) {
      console.log("onFile", event);
      this.selectedFile = event.target.files[0];
    },
    async onUpload() {
      // console.log("onUpload", this.selectedFile);

      const fd = new FormData();
      fd.append("image", this.selectedFile);

      const res = await this.$store.uploadAsync(fd);
      console.log("onUpload ret", res);

      this.resultMessage = res.message;
    },
  },
};
</script>
